const citySelect = {
	/* eslint-disable */
	firstletter: '',
	/**
	 * 获取汉字的拼音首字母
	 * @param str 汉字字符串，如果遇到非汉字则原样返回
	 * @return 返回对象 {unicode:NUmber,firstletter：String}
	 */
	getFirstLetter: (str) => {
		if (!str || /^ +$/g.test(str)) {
			return ''
		}

		// 使用首字母字典文件
		if (citySelect.firstletter) {
			const result = []
			const unicode = str.charCodeAt(0)
			let ch = str.charAt(0)
			if (unicode >= 19968 && unicode <= 40869) {
				ch = citySelect.firstletter.charAt(unicode - 19968)
			} else if ((unicode >= 97 && unicode <= 122) || (unicode >= 65 && unicode <= 90)) {
				ch = ch.toLocaleUpperCase()
			} else {
				ch = '#'
			}
			const obj = {
				unicode: unicode,
				firstletter: ch
			}
			return obj
		}
		return ''
	}
}

export default citySelect
